import { scribeFactory } from "../../services/scribe";
import {
  START_RECORDING,
  STOP_RECORDING,
  PAUSE_RECORDING,
  RESUME_RECORDING,
  SET_TRANSCRIPT,
  SET_ERROR,
} from "./scribeActionConstants";

// Initialize processor reference
let processorRef = null;

function createCallback(dispatch) {
  return (data) => {
    if (data && typeof data === "object") {
      const sortedValues = Object.entries(data)
        .sort(([keyA], [keyB]) => Number(keyA) - Number(keyB))
        .map(([_, value]) => value);
      const transcript = sortedValues.join(" ");
      dispatch({ type: SET_TRANSCRIPT, payload: transcript });
    } else {
      console.error("Invalid data received:", data);
      dispatch({ type: SET_ERROR, payload: "Invalid data received" });
    }
  };
}

export const startRecording = () => async (dispatch) => {
  try {
    const mode = localStorage.getItem("API_MODE") || "default";
    const ScribeProcessor = scribeFactory({ live: mode });

    processorRef = new ScribeProcessor(
      process.env.REACT_APP_WS_URL,
      process.env.REACT_APP_SERVER_URL,
      createCallback(dispatch)
    );

    await processorRef.getMicrophoneAccess(
      () => {
        processorRef.startRecording();
        dispatch({ type: START_RECORDING });
        dispatch({ type: SET_ERROR, payload: null });
      },
      (error) => {
        console.error("Error while accessing microphone:", error);
        dispatch({ type: SET_ERROR, payload: "Microphone access error" });
      }
    );
  } catch (error) {
    console.error("Error starting recording:", error);
    dispatch({ type: SET_ERROR, payload: "Failed to start recording" });
  }
};

export const stopRecording = () => async (dispatch) => {
  if (!processorRef) {
    dispatch({ type: SET_ERROR, payload: "Processor not initialized" });
    return;
  }

  try {
    const newTranscript = await processorRef.stopRecording();
    dispatch({ type: STOP_RECORDING, payload: newTranscript });
    processorRef = null; // Reset the processor after stopping
  } catch (error) {
    console.error("Error stopping recording:", error);
    dispatch({ type: SET_ERROR, payload: "Failed to stop recording" });
  }
};

export const pauseRecording = () => (dispatch) => {
  if (processorRef) {
    processorRef.pauseRecording();
    dispatch({ type: PAUSE_RECORDING });
  } else {
    dispatch({ type: SET_ERROR, payload: "Processor not initialized" });
  }
};

export const resumeRecording = () => (dispatch) => {
  if (processorRef) {
    processorRef.resumeRecording();
    dispatch({ type: RESUME_RECORDING });
  } else {
    dispatch({ type: SET_ERROR, payload: "Processor not initialized" });
  }
};

export const setTranscript = (text) => ({
  type: SET_TRANSCRIPT,
  payload: text,
});
