import React, { useEffect, useState } from "react";
import "./StreamingIndex.scss";
import { useSelector, useDispatch } from "react-redux";
import {
  startRecording,
  stopRecording,
  pauseRecording,
  resumeRecording,
} from "Reducers/scribeReducer/scribeActions";

export const StreamingIndex = () => {
  const dispatch = useDispatch();

  const isRecording = useSelector((state) => state.scribe.isRecording);
  const isPaused = useSelector((state) => state.scribe.isPaused);
  const transcriptText = useSelector((state) => state.scribe.transcriptText);

  const linkType = useSelector((state) => state.data.linkType);
  const demoGraphicId = useSelector((state) => state.data.demoGraphicId);
  const [id, setId] = useState("");

  useEffect(() => {
    setId(demoGraphicId);
  }, [demoGraphicId]);

  const handleStart = () => {
    dispatch(startRecording());
  };

  const handleStop = () => {
    dispatch(stopRecording());
  };

  const handlePause = () => {
    dispatch(pauseRecording());
  };

  const handleResume = () => {
    dispatch(resumeRecording());
  };

  return (
    <div className="streaming-index">
      <div className="transcription-container">
        <h3>Live Transcription:</h3>
        <p className="transcript-text">{transcriptText}</p>
      </div>
      <p>{demoGraphicId || "No demographic ID received."}</p>
      <p>{linkType || "No Link."}</p>
      <div className="button-container">
        {!isRecording ? (
          <button onClick={handleStart} className="myextBtn">
            Turn On Mic
          </button>
        ) : isPaused ? (
          <button onClick={handleResume} className="myextBtn">
            Resume
          </button>
        ) : (
          <>
            <button onClick={handlePause} className="myextBtn">
              Pause
            </button>
            <button onClick={handleStop} className="myextBtn">
              Turn Off Mic
            </button>
          </>
        )}
      </div>
    </div>
  );
};
