import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  startRecording,
  stopRecording,
  pauseRecording,
  resumeRecording,
} from "Reducers/scribeReducer/scribeActions";
import "./Scribe.scss";

const Scribe = () => {
  const dispatch = useDispatch();
  const { isRecording, isPaused, transcriptText } = useSelector(
    (state) => state.scribe
  );
  const { patientName, demoGraphicId } = useSelector((state) => state.data);

  const [selectedTab, setSelectedTab] = useState("session");
  const [timer, setTimer] = useState(0);

  const getInitials = (name) => {
    if (!name) return "";
    return name
      .split(",")
      .map((n) => n.trim().charAt(0).toUpperCase())
      .join("");
  };

  const handleRecording = (action) => {
    dispatch(action);
  };

  useEffect(() => {
    let interval = null;
    if (isRecording) {
      interval = setInterval(() => {
        setTimer((prevTimer) => prevTimer + 1);
      }, 1000);
    } else {
      clearInterval(interval);
    }
    return () => clearInterval(interval);
  }, [isRecording]);

  const formatTimer = (seconds) => {
    const minutes = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${minutes.toString().padStart(2, "0")}:${secs
      .toString()
      .padStart(2, "0")}`;
  };

  useEffect(() => {
    if (!isRecording) {
      setTimer(0);
    }
  }, [isRecording]);

  return (
    <div className="scribe-container">
      <div className="top-container">
        <p className="patientName">{patientName || "None"}</p>
        <div className="detailsContainer">
          <p>{demoGraphicId || "None"}</p>
          <p>{demoGraphicId || "None"}</p>
          <p>Other Details</p>
        </div>
        <div className="tabContainer">
          {["session", "transcript", "summary"].map((tab) => (
            <div
              key={tab}
              className={`tabOption ${
                selectedTab === tab ? "selectedTab" : ""
              }`}
              onClick={() => setSelectedTab(tab)}
            >
              {tab.charAt(0).toUpperCase() + tab.slice(1)}
            </div>
          ))}
        </div>
      </div>

      <div className="recordingContainer">
        {selectedTab === "session" && (
          <div className="innerRecordingContainer">
            <div className="sessionData">
              <div className="timerContainer">
                <p>{formatTimer(timer)}</p>
              </div>
              <div className="profileContainer">
                <div className="profile">
                  <p>{getInitials(patientName)}</p>
                </div>
              </div>
              <div className="animationContainer"></div>
            </div>
          </div>
        )}
        {selectedTab === "transcript" && (
          <div className="textContainer">
            {transcriptText || "No transcription available."}
          </div>
        )}
        {selectedTab === "summary" && (
          <div className="textContainer">
            <p>AI Summary will be displayed here.</p>
          </div>
        )}

        {selectedTab === "session" && !isPaused && (
          <div className="buttonContainer">
            {!isRecording ? (
              <button
                onClick={() => handleRecording(startRecording())}
                className="scribe-button"
              >
                Turn On Mic
              </button>
            ) : isPaused ? (
              <button
                onClick={() => handleRecording(resumeRecording())}
                className="scribe-button"
              >
                Resume
              </button>
            ) : (
              <>
                <button
                  onClick={() => handleRecording(pauseRecording())}
                  className="scribe-button"
                >
                  Pause
                </button>
                <button
                  onClick={() => handleRecording(stopRecording())}
                  className="scribe-button"
                >
                  Turn Off Mic
                </button>
              </>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default Scribe;
