import { DATA_CONSTANTS } from "./dataActionConstants";

const initialState = {
  linkType: "Clinic",
  demoGraphicId: "",
  patientName: "Nathan,Kasi",
};

const dataReducer = (state = initialState, action) => {
  switch (action.type) {
    case DATA_CONSTANTS.SET_LINK_TYPE:
      return { ...state, linkType: action.payload };
    case DATA_CONSTANTS.SET_DEMOGRAPHIC_ID:
      return { ...state, demoGraphicId: action.payload };
    case DATA_CONSTANTS.SET_PATIENT_NAME:
      return { ...state, patientName: action.payload };
    default:
      return state;
  }
};

export default dataReducer;
