import {
  START_RECORDING,
  STOP_RECORDING,
  PAUSE_RECORDING,
  RESUME_RECORDING,
  SET_TRANSCRIPT,
  SET_ERROR,
} from "./scribeActionConstants";

const data =
  "Doctor, I've been feeling unwell recently — headaches, a sore throat, and now a fever. I’m not sure if it's something serious.I understand; those symptoms could point to a few things, like a viral infection, especially if it's paired with a fever. Have you had any exposure to someone who’s sick or traveled recently? Let's run a quick exam and possibly a test to pinpoint the cause, so we can get you feeling better soon.";

const initialState = {
  transcriptText: data,
  isRecording: false,
  isPaused: false,
  error: null,
};

export default function scribeReducer(state = initialState, action) {
  switch (action.type) {
    case START_RECORDING:
      return { ...state, isRecording: true, isPaused: false, error: null };
    case STOP_RECORDING:
      return {
        ...state,
        isRecording: false,
        isPaused: false,
        transcriptText: action.payload,
        error: null,
      };
    case PAUSE_RECORDING:
      return { ...state, isPaused: true };
    case RESUME_RECORDING:
      return { ...state, isPaused: false };
    case SET_TRANSCRIPT:
      return { ...state, transcriptText: action.payload };
    case SET_ERROR:
      return { ...state, error: action.payload };
    default:
      return state;
  }
}
