const { DATA_CONSTANTS } = require("./dataActionConstants");

export const setDataLinkType = (data) => ({
  type: DATA_CONSTANTS.SET_LINK_TYPE,
  payload: data,
});

export const setDataDemographicid = (data) => ({
  type: DATA_CONSTANTS.SET_DEMOGRAPHIC_ID,
  payload: data,
});

export const setPatientName = (data) => ({
  type: DATA_CONSTANTS.SET_PATIENT_NAME,
  payload: data,
});
