import "./App.css";
import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
// Imported components
import Loader from "components/Loader/Loader";
// Imported pages
import PhoneIndex from "pages/phoneIndex/PhoneIndex";
import Index from "pages/Index/Index";
import { StreamingIndex } from "pages/StreamingPage/StreamingIndex";
import Scribe from "pages/scribe/Scribe";

import {
  setDataLinkType,
  setDataDemographicid,
  setPatientName,
} from "Reducers/dataReducer/dataActions";

const MainContent = () => {
  const [isLoading, setIsLoading] = useState(true);
  const dispatch = useDispatch();
  const linkType = useSelector((state) => state.data.linkType);

  useEffect(() => {
    if (linkType !== undefined && linkType !== null) {
      setIsLoading(false);
    }
  }, [linkType]);

  useEffect(() => {
    const handleMessage = (event) => {
      if (event.source !== window) return;
      if (event.data.type === "UPDATE_DEMOGRAPHIC_INFO") {
        dispatch(setDataDemographicid(event.data.demographicId));
        dispatch(setDataLinkType(event.data.linkType));
        dispatch(setPatientName(event.data.name));
      }
    };

    window.addEventListener("message", handleMessage);

    return () => {
      window.removeEventListener("message", handleMessage);
    };
  }, [dispatch]);

  // Mapping linkType to components
  const renderComponent = () => {
    switch (linkType) {
      case "Video call":
        return <StreamingIndex />;
      case "Phone call":
        return <PhoneIndex />;
      case "Clinic":
        return <Scribe />;
      default:
        return <Index />;
    }
  };

  return <>{isLoading ? <Loader /> : renderComponent()}</>;
};

export default MainContent;
